<template>
  <div>
    <VueHtml2pdf
      ref="html2Pdf"
      :show-layout="false"
      :float-layout="true"
      :enable-download="false"
      :preview-modal="true"
      :paginate-elements-by-height="3508"
      :filename="'DJ-' + reportData.dni"
      :pdf-quality="2"
      :manual-pagination="false"
      pdf-format="a4"
      pdf-orientation="portrait"
      pdf-content-width="800px"
      class="h-full"
      @progress="$emit('progress', $event)"
      @hasStartedGeneration="$emit('started', $event)"
      @hasDownloaded="dowloading"
    >
      <section slot="pdf-content" class="px-12 mt-0 pt-20">
        <!-- PDF Content Here -->
        <div class="flex flex-col text-xs font-normal">
          <div class="flex justify-between items-center">
            <div class="w-32">
              <img src="@//assets//images//unap.jpg" alt="" />
            </div>

            <div class="uppercase font-bold text-right">
              <p>dirección de admisión</p>               
            </div>
          </div>

          <div class="font-bold text-2xl uppercase text-center mt-10">
            <p>Declaración Jurada</p>
          </div>

          <div>
            <p class="text-left mt-10">
              Conste en el presente documento que yo,
              <b>{{
                reportData.apellido_paterno +
                  " " +
                  reportData.apellido_materno +
                  " " +
                  reportData.nombre
              }}</b
              >, identificado con Documento de Identidad N°
              <b>{{ reportData.dni }}</b>
              y domiciliado en
              <b>{{ reportData.direccion }}</b>
              de esta ciudad, postulante al programa de estudio de
              <b>{{ reportData.escuela }}</b>
              mediante la modalidad
              <b>{{ reportData.modalidad }}</b
              >, expediente N°
              <b>{{ reportData.id }}</b>
              en el proceso de Admisión de la Universidad Nacional de la
              Amazonía Peruana, correspondiente al
              <b>{{ reportData.admision }}</b
              >, declaro libre y espontánea voluntad, conocer y aceptar todos y
              cada uno de los articulos establecidos en el
              <b>Reglamento de Admisión</b>.
            </p>
          </div>

          <div class="mt-10 flex justify-between">
            <p></p>
            <p>Iquitos, {{ getTextFecha(reportData.fecha) }}</p>
          </div>

          <div
            class="mt-20 text-center border-t-2 border-black border-dashed w-1/2 m-auto"
          >
            <b> Firma del Postulante</b>
            <p>
              D.N.I: <b>{{ reportData.dni }}</b>
            </p>
          </div>

          <div class="flex justify-between mt-64">
            <div class="w-1/2 border-t-2 border-black border-dashed">
              <p>
                <b>Firma del Padre o Tutor (para menor de edad)</b>
              </p>
              <p>Nombre del padre o tutor:</p>
              <div class="flex justify-between">
                <p>D.N.I:</p>
                <p>Teléfono:</p>
              </div>
              <p>Domicilio:</p>
            </div>

            <div class="px-5"></div>

            <div
              class="w-1/2 text-center border-t-2 border-black border-dashed"
            >
              <p><b>Jefe DA</b></p>
            </div>
          </div>
        </div>
      </section>
    </VueHtml2pdf>
  </div>
</template>

<script>
const VueHtml2pdf = () => import("vue-html2pdf");
import axios from "@//plugins//axios";

export default {
  components: {
    VueHtml2pdf,
  },

  data() {
    return {
      reportData: {},
    };
  },

  async mounted() {
    await this.getReport();
    this.$refs.html2Pdf.generatePdf();
  },

  methods: {
    getTextFecha(val) {
      const meses = [
        "Enero",
        "Febrero",
        "Marzo",
        "Abril",
        "Mayo",
        "Junio",
        "Julio",
        "Agosto",
        "Setiembre",
        "Octubre",
        "Noviembre",
        "Diciembre",
      ];
      try {
        const date = val.split("-");
        let anio, mes, dia;
        dia = date[0];
        mes = date[1];
        anio = date[2];
        mes = meses[mes - 1];
        return `${dia} de ${mes} del ${anio}.`;
      } catch (error) {
        return "";
      }
    },

    async getReport() {
      try {
        const { id } = this.$route.params;
        if (!id) return;

        const { data } = await axios.get("get_declaracion_jurada_by_id", {
          params: { id },
        });

        this.reportData = data[0];
      } catch (error) {
        console.error(error);
      }
    },

    dowloading(e) {
      this.$emit("dowloading", e);
    },
  },
};
</script>
